@media only screen and (max-width: 399px) {
  .spacer-space {
    height: 2vh;
  }
  .spacer-container {
    position: relative;
    height: 50vh;
    width: 100%;
    background-color: #282828;
  }
  .spacer-container .spacer-text {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    font-family: "Mulish", sans-serif;
    font-size: 1.75vh;
    color: #fff;
  }
  .spacer-container .spacer-text a {
    color: #fff;
    cursor: pointer;
  }
  .spacer-container .spacer-span-one {
    position: absolute;
    top: 0;
    right: 0;
    height: 1.75vh;
    width: 80%;
    background-color: #adabdd;
  }
  .spacer-container .spacer-span-two {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 1.75vh;
    width: 70%;
    background-color: #ffafbf;
  }
  .spacer-container .spacer-span-three {
    position: absolute;
    bottom: 1.75vh;
    left: 0;
    height: 1.75vh;
    width: 50%;
    background-color: #ffafbf;
  }
  .spacer-container .spacer-span-four {
    position: absolute;
    top: 1.75vh;
    left: 4vw;
    height: 1.75vh;
    width: 30%;
    background-color: #adabdd;
  }
  .spacer-container .spacer-span-five {
    position: absolute;
    top: 1.75vh;
    right: 3vw;
    height: 1.75vh;
    width: 40%;
    background-color: #adabdd;
  }
  .spacer-container .spacer-span-six {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 18vh;
    width: 1vw;
    background-color: #ffafbf;
  }
  .spacer-container .spacer-span-seven {
    position: absolute;
    bottom: 30%;
    left: 0;
    height: 18vh;
    width: 1.25vw;
    background-color: #ffafbf;
  }
}

