@import "../variables";

@media only screen and (max-width: 499px) {
  .intro-spacer {
    height: 2.5vh;
  }
  .intro-container {
    height: 110vh;
    width: 100%;
    background-color: $lightorange;

    .intro {
      padding: 12vh 0 13vh 0;
      height: 100%;
      max-width: 90%;
      margin: 0 auto;
      display: grid;
      grid-gap: 4vh;
      grid-template-rows: 1fr 1.5fr;

      .intro-img {
        height: 100%;
        width: 100%;
        max-width: 252px;
        margin: 0 auto;

        background: url("./image2.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;

        .intro-img-span-one {
          position: absolute;
          top: 0;
          right: 0;
          height: 20%;
          width: 12%;
          background-color: $lightorange;
        }

        .intro-img-span-two {
          position: absolute;
          bottom: 0;
          right: 0;
          height: 40%;
          width: 5%;
          background-color: $lightorange;
          transform: translateY(-20%);
        }

        .intro-img-span-three {
          position: absolute;
          bottom: 0;
          left: 0;
          height: 20%;
          width: 5%;
          background-color: $lightorange;
        }

        .intro-img-span-four {
          position: absolute;
          bottom: 0;
          left: 0;
          height: 8%;
          width: 16%;
          background-color: $lightorange;
        }
      }

      .intro-text {
        grid-area: 1;
        position: relative;
        width: 100%;
        max-width: 252px;
        margin: 0 auto;

        .intro-text-p {
          font-family: "Mulish", sans-serif;
          line-height: 1.4;
          color: $textcolor;
          position: absolute;
          bottom: 0;
          left: 0;
          font-size: 2.5vh;
        }
      }
    }
  }
}

