@media only screen and (min-width: 1000px) and (max-width: 1299px) {
  .intro-spacer {
    height: 4vh;
  }
  .intro-container {
    background-color: $lightorange;
    height: 110vh;
    width: 100%;
    .intro {
      padding: 12vh 0 5vh 0;
      height: 90%;
      width: 90%;
      max-width: 1200px;
      margin: 0 auto;
      display: grid;
      grid-gap: 6vw;
      grid-template-columns: repeat(2, 1fr);

      .intro-img {
        background: url("./image2.jpg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;

        .intro-img-span-one {
          position: absolute;
          top: 0;
          right: 0;
          height: 20%;
          width: 12%;
          background-color: $lightorange;
        }

        .intro-img-span-two {
          position: absolute;
          bottom: 0;
          right: 0;
          height: 40%;
          width: 5%;
          background-color: $lightorange;
          transform: translateY(-20%);
        }

        .intro-img-span-three {
          position: absolute;
          bottom: 0;
          left: 0;
          height: 20%;
          width: 5%;
          background-color: $lightorange;
        }

        .intro-img-span-four {
          position: absolute;
          bottom: 0;
          left: 0;
          height: 8%;
          width: 16%;
          background-color: $lightorange;
        }
      }

      .intro-text {
        height: 90%;
        font-family: "Mulish", sans-serif;
        line-height: 1.6;
        color: $textcolor;
        position: relative;
        font-size: 4.25vh;
        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          right: 0;
          height: 1vh;
          width: 60%;
          background-color: $barcolor;
        }
      }
    }
  }
}

