@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.coin-app {
  // border: 1px solid rgb(0, 100, 0);
  background-color: #1a1a1c;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 40px;

  .coin-search {
    width: 100%;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  form {
    display: flex;
    align-items: center;
    justify-content: center;

    .coin-input {
      outline: none;
      padding-left: 16px;
      height: 40px;
      width: 70%;
      border-radius: 4px;
      border: none;
      background-image: linear-gradient(
        -225deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
      );
    }

    color: #e2e2e2;
  }

  .coin-input::placeholder {
    color: #e2e2e2;
  }

  .coin-text {
    margin-bottom: 10px;
    font-size: 14px;
    text-align: center;
  }
}

